import React, {createContext, useState} from 'react';
import initialStore from "utils/initialStore";
import uniqueId from "utils/uniqueId.js";

// export the context so that other components can import
export const StoreContext = createContext(); 

function StoreContextProvider(props){
    const [store, setStore] = useState(initialStore);
    function addLike(postId) {
      const like = {
        userId: store.currentUserId,
        postId,
        datetime: new Date().toISOString()
      };
  
      setStore({
        ...store,
        likes: store.likes.concat(like)
      });
    }
  
    function addFollower(userId, followerId){
      // use concat
      const follow = {
        userId: userId,
        followerId: followerId
      }
      setStore({
        ...store,
        followers: store.followers.concat(follow)
      });
    }
    function removeFollower(userId, followerId){
      // use filter
      setStore({
        ...store,
        followers: store.followers.filter(
          follow => !(follow.userId === userId && follow.followerId === followerId)
        )
      });
    }
  
    function removeLike(postId) {
      // console.log('removeLike',postId, store.likes.filter(like=>!(like.userId===store.currentUserId && like.postId===postId)));
      setStore({
        ...store,
        likes: store.likes.filter(
          like => !(like.userId === store.currentUserId && like.postId === postId)
        )
      });
    }
  
    function addComment(postId, text) {
      const comment = {
        userId: store.currentUserId,
        postId,
        text,
        datetime: new Date().toISOString()
      };
      setStore({
        ...store,
        comments: store.comments.concat(comment)
      });
    }
    function addPost(photo, desc) {
      const post = {
        id: uniqueId("post"),
        userId: store.currentUserId,
        photo,
        desc,
        datetime: new Date().toISOString()
      };
      setStore({
        ...store,
        posts: store.posts.concat(post)
      });
      
    }

	return (
        <StoreContext.Provider value = {{...store, addComment, addLike, removeLike, addPost, addFollower, removeFollower}}>
            {props.children}
        </StoreContext.Provider>
    )
}
export default StoreContextProvider; // export this component as default